import { useContext } from "react";
import styled from "styled-components";
import { ExternalLink } from "react-external-link";

import { WeatherContext } from "../WeatherContext";
import { RiCloseCircleLine } from "react-icons/ri";

const About = () => {
    const { setShowAbout } = useContext(WeatherContext);
    const fermerBoite = () => setShowAbout(false);

    return (
        <Wrapper>
            <button onClick={fermerBoite}>
                <RiCloseCircleLine size={30} title="Fermer" />
            </button>
            <p>Mto pour emporter est un miniprojet pour tendre mes connaissances en dveloppement web, tout en rpondant  un besoin existant. Cette application vous permet de rechercher les prvisions mto pour plusieurs villes le long de votre itinraire. C'est parfait pour une croisire, un <span>road trip</span>, un priple de cyclotourisme, ou tout autre voyage multivilles. Puisque les pronostics changent rapidement, les donnes ne seront conserves en stockage local dans votre navigateur que pendant environ une heure.</p>
            <p>Les prvisions sont ralises  l'aide de l'<span>API</span> <ExternalLink href="https://www.weatherbit.io/">Weatherbit</ExternalLink>, et sont mises  jour frquemment. Si vous remarquez des problmes avec ce site web, n'hsitez pas  me contacter <ExternalLink href="https://github.com/chinook07/meteo-pour-emporter">via GitHub</ExternalLink>.</p>
        </Wrapper>
    )
}

const Wrapper = styled.aside`
    background-color: var(--c-yellow);
    color: var(--c-dark);
    padding: 15px;
    button {
        background-color: inherit;
        border: none;
        cursor: pointer;
        display: flex;
        margin: 0 0 0 auto;
        &:hover {
            background-color: var(--c-dark);
            color: var(--c-yellow);
        }
    }
    p {
        margin-top: 5px;
        span {
            font-style: italic;
        }
        a {
            text-decoration: none;
        }
    }
`

export default About;